.App {
  font-family: sans-serif;
  text-align: center;
  margin: 0 auto;
  width: 50%;
}

.App .modal {
  text-align: left;
}

.Header {
  height: 60px;
  padding: 10px;
}

.Header .buttons {
  position: absolute;
  right: 50px;
}

.Header .buttons .btn {
  margin-left: 10px;
}

.EventListItem .desc {
  overflow: hidden;
  max-height: 3em;
  margin-bottom: 5px;
}

.EventListItem .card-btns {
  margin-bottom: 10px;
}

.EventListItem .card-btns button {
  margin-left: 10px;
}

.EventListItem image {
  object-fit: contain;
}

.eventDetailsImage {
  text-align: center;
  margin: 10px 0;
}

.eventDetailsImage img {
  max-width: 200px;
}

@media (max-width: 900px) {
  .App {
    width: 80%;
  }
}

@media (max-width: 600px) {
  .App {
    width: 90%;
  }
}
